.logo {
    color: rgb(255, 255, 255);
    font-size: 25px;
    padding: 15px;
}
.pages ul li{
    font-size: 15px;
    justify-content: center;
}
nav {
    background-color: #0ca8f1;
    padding: -1px;
    display: flex;
    justify-content: space-around;
  }
  
  ul {
    list-style-type: none;
    margin: 0;
    padding: 25px;
  }
  
  li {
    display: inline;
    margin-right: 20px;
  }
  
  li:last-child {
    margin-right: 0;
  }
  
  a {
    color: #fff;
    text-decoration: none;
  }
  
  a:hover {
    color: #000000;
  }
  
  .container {
    max-width: 800px;
    margin: 20px auto;
    padding: 0 20px;
  }
  
  h2 {
    color: #333;
  }
  
  p {
    color: #666;
  }

.home h2{
  color: rgb(22, 74, 216);
}

.contact h2{
  color: rgb(22, 74, 216);
}

.about h2{
  color: rgb(22, 74, 216);
}
